import { Component, OnInit } from '@angular/core'
import { AssetModelCategoryMasterService } from 'src/app/sdk/asset/services'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Model_category_master } from 'src/app/sdk/asset/models'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private assetModelCategoryMasterService: AssetModelCategoryMasterService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    data: Model_category_master
    id_model_category_master: string = ''

    ngOnInit() {
        this.id_model_category_master = this.activatedRoute.snapshot.paramMap.get(
            'id_model_category_master',
        )
        this.form()
        this.getData()
    }

    getData() {
        this.assetModelCategoryMasterService
            .getAssetModelCategoryMastersId(this.id_model_category_master)
            .subscribe(data => {
                this.dataForm.setValue(data.data)
            })
    }

    dataForm: FormGroupTypeSafe<Model_category_master>
    form() {
        this.dataForm = this.fb.group<Model_category_master>({
            name_model_category: new FormControl('', [Validators.required]),
            model_category_desc: new FormControl(''),
            id_model_category_master: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.assetModelCategoryMasterService
            .putAssetModelCategoryMastersId({ id: this.id_model_category_master, body: this.data })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/asset/master/category/list'])
    }
}
