import { Model_category_master } from 'src/app/sdk/asset/models/model-_category-_master'
import { Component, OnInit } from '@angular/core'
import { AssetModelCategoryMasterService } from 'src/app/sdk/asset/services'
import { FilterFuctionService } from 'src/app/services/filter-fuction.service'
import { NzMessageService } from 'ng-zorro-antd'
import { NgxSpinnerService } from 'ngx-spinner'

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss'],
})
export class ListComponent implements OnInit {
    constructor(
        private assetModelCategoryMasterService: AssetModelCategoryMasterService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) { }

    ngOnInit() {
        this.getData()
    }

    listOfDisplayData: Model_category_master[] = []
    listOfData: Model_category_master[] = []
    getData() {
        this.spinner.show()
        this.assetModelCategoryMasterService.getAssetModelCategoryMasters(JSON.stringify({sortKey: this.sortKey, sortValue:this.sortValue, search: this.search})).subscribe(data => {
            this.listOfDisplayData = data.data
            this.listOfData = data.data
            this.spinner.hide()
        })
    }


    sortKey: string | null = null
    sortValue: string | null = null
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }
    search: string = ''


    delete(id) {
        this.spinner.show()
        this.assetModelCategoryMasterService
            .deleteAssetModelCategoryMastersId(id)
            .subscribe(data => {
                this.getData()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }
}
